/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url(/fonts/fontstyle.css);
html, body {
  box-sizing: border-box; }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html, body {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  background-color: #000000;
  color: #FFFFFF;
  box-sizing: border-box;
  font-size: 16px;
  max-width: 100%; }
  html img, body img {
    max-width: 100%;
    max-height: 100%; }

/* Header on-change classes */
.new-size {
  height: 200px; }
  @media (min-width: 600px) {
    .new-size {
      height: 150px; } }
  @media (min-width: 1240px) {
    .new-size {
      height: 150px; } }

.new-logo {
  width: 105px;
  height: 105px;
  padding: 20px 0 0 0; }
  @media (min-width: 600px) {
    .new-logo {
      padding: 0 52.5px 0 0; } }
  @media (min-width: 1240px) {
    .new-logo {
      padding: 0 52.5px 0 0; } }

/* Header */
.intro-webpage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.25s ease-in-out 0.25s; }
  .intro-webpage.default-size {
    height: 85vh; }
  @media (min-width: 600px) {
    .intro-webpage {
      flex-direction: row; } }
  @media (min-width: 1240px) {
    .intro-webpage {
      justify-content: flex-start;
      padding-left: 75px; } }
  .intro-webpage .nyt-logo.logo-size {
    width: 225px;
    height: 225px; }
    @media (min-width: 600px) {
      .intro-webpage .nyt-logo.logo-size {
        width: 210px;
        height: 210px;
        padding: 0 30px 0 0; } }
    @media (min-width: 1240px) {
      .intro-webpage .nyt-logo.logo-size {
        width: 225px;
        height: 225px; } }
  .intro-webpage p {
    padding: 15px 0; }
  .intro-webpage .selector {
    text-align: center;
    padding-bottom: 15px; }
    @media (min-width: 600px) {
      .intro-webpage .selector {
        text-align: left; } }
    .intro-webpage .selector .dropdown-section {
      border-radius: 5px;
      border: none;
      text-align: center; }
  .intro-webpage .preload {
    display: none;
    align-self: center;
    width: 37.5px;
    height: 37.5px;
    padding-top: 15px; }
    @media (min-width: 600px) {
      .intro-webpage .preload {
        align-self: center; } }

/* Body */
section .news-content {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(12, 1fr); }
  @media (min-width: 600px) {
    section .news-content {
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(4, 1fr); } }
  @media (min-width: 1240px) {
    section .news-content {
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(3, 1fr); } }

li a, li a:link, li a:visited {
  text-decoration: none;
  color: #FFFFFF; }
  li a article, li a:link article, li a:visited article {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    height: 350px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; }
    @media (min-width: 600px) {
      li a article, li a:link article, li a:visited article {
        width: 100%;
        height: 450px; } }
    @media (min-width: 1240px) {
      li a article, li a:link article, li a:visited article {
        width: 100%;
        height: 430px; } }
    li a article .text-content, li a:link article .text-content, li a:visited article .text-content {
      height: auto;
      background-color: rgba(0, 0, 0, 0.5);
      padding: 10px 5px;
      line-height: 25px; }

/* Footer */
footer {
  color: #C2C2C2;
  text-align: center; }
  @media (min-width: 1240px) {
    footer {
      text-align: left;
      padding-left: 75px; } }
  footer p {
    padding: 37.5px 0; }
